/* Hero Section */
.b_first {
	background-color: #6a706e;
	background-image: url("images/urbantaichi-bg.jpg");
	background-size: cover;
	background-position: center;
	background-repeat: no-repeat;
}

.b_first .blog_first {
	margin-top: 0;
}

.b_right {
	min-height: 500px;
}

@media only screen and (max-width: 600px) {
	.b_right {
		min-height: unset;
	}
}

/* Homepage Blog Section */
@media only screen and (max-width: 600px) {
	#blogs .home_article .home_article-cat {
		width: 100%;
		margin-right: 0;
	}
}

#blogs .home_article .home_article-cat h3 {
	margin-bottom: 1rem;
}
